/_ SOCIAL STUFF _/
.social-container {
  background: black;
  padding: 25px 50px;
}


a.social {
  margin: 0 1rem;
  transition: transform 250ms;
  display: inline-block;
}

a.social:hover {
  transform: translateY(-2px);
}


a.youtube {
  color: #eb3223;
}

a.facebook {
  color: #4968ad;
}

a.twitter {
  color: #49a1eb;
}

a.instagram {
  color: pink;
}
.line>h6{
	color: #00b16a;
	border-bottom: 2px solid white;

}













.clr{
	display: inline-flex;
	background: black;
}

h2{
	text-align: left;
	font-weight:700;
	margin: 0;
	font-size: 2.5rem;
	font-family: inherit;
	line-height: 1.2;

}


.list{
	margin-top: 20px;
}
ul, ul li{
	list-style: none;
	padding: 0;
	margin:0;
}
ul{
	display: block;
	margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
}
.list li {
    line-height: 30px;
    color: #fff;
}
li {
    display: list-item;
    text-align: -webkit-match-parent;
}
.list li i{
	color: #00B16A;
	margin-right: 15px;

}
.fa-phone:before {
    content: "\f095";
}
.fa-envelope:before {
    content: "\f0e0";
}
.fa-map-o:before {
    content: "\f278";
}
 ::after, ::before {
    box-sizing: border-box;
}
.fa {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    box-sizing: inherit;
    -webkit-font-smoothing: antialiased;
 }
 .logo > h2{
 	
 	text-decoration-color: green;
 	text-decoration-style: solid;
 	padding-top: 10px;
 	color:#00b16a;
 }